import React, { Component } from 'react';
import './PetitionGenerator.css';

const initialState = {
    email: '',
    name: '',
    address: '',
    courtName: '',
    caseNumber: '',

    nameError: '',
    addressError: '',
    courtNameError: '',
    caseNumberError: '',
    submitted: false,
}

class PetitionGenerator extends Component{
    constructor(props) {
        super(props);
        this.state = initialState;

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    validate(){
        let nameError = '';
        let addressError = '';
        let courtNameError = '';
        let caseNumberError = '';

        if (!this.state.name ){
            nameError = 'Укажите имя'
        }

        if (!this.state.address){
            addressError = 'Укажите адрес'
        //    this.setState({formErrors: {address:'Укажите адрес'}});
        }

        if (!this.state.courtName ){
            courtNameError = 'Укажите номер и адрес участка'
        }

        if (!this.state.caseNumber ){
            caseNumberError = 'Укажите статью и часть КоАП РФ'
        }

        if (nameError || addressError || courtNameError || caseNumberError){
            this.setState({nameError, addressError, courtNameError, caseNumberError});
            return false
        }

        return true;

    }

    handleSubmit(event){
        event.preventDefault();
        const isValid = this.validate();
        if (isValid) {
            console.log('Поля формы заполнены', this.state);

            //clear form
//            this.setState(initialState);
            this.setState({ submitted: true })
        }
        console.log('Ошибка ', this.state);

    }

    handleChange(event){
        this.setState({
            [event.target.name]: event.target.value});
    }

    render() {
        if(this.state.submitted){
            return(
                <div className='center'>
                    <a href='localhost'>Назад</a>
                    <p style={{textAlign:"right"}}>Мировому судье судебного участка <br></br>
                        {this.state.courtName} <br></br>
                        от {this.state.name} <br></br>
                        зарегистрированного(ой) по адресу <br></br>
                        {this.state.address} <br></br>
                    </p>
                    <h3 style={{textAlign:"center"}}>
                        Ходатайство<br></br>
                        о рассмотрении дела об административном правонарушении<br></br>
                        по месту жительства
                    </h3>
                    <p>
                        В Вашем производстве находится дело об административном правонарушении, предусмотренное {this.state.caseNumber}, в отношении меня, {this.state.name}.<br></br>
                        Согласно ч.1 ст.29.5 КоАП РФ “По ходатайству лица, в отношении которого ведется производство по делу об административном правонарушении, дело может быть рассмотрено по месту жительства данного лица”.<br></br>
                        В настоящее время я зарегистрирован и постоянно проживаю по адресу: {this.state.address}. На основании изложенного, в соответствии с ч.1 ст.29.5 КоАП РФ,
                    </p>
                    <h4 style={{textAlign:"center"}}>
                        Прошу:
                    </h4>
                    <p>
                    Направить дело об административном правонарушении, возбужденное в отношении меня по {this.state.caseNumber} КоАП РФ в судебный участок по моему месту жительства.
                    В случае отказа в удовлетворении ходатайства прошу в соответствии с требованиями ч.2 ст.24.4 КоАП РФ и с учетом требований ст.29.12 КоАП РФ вынести решение в виде мотивированного определения и приобщить его к материалам дела.
                    Данное ходатайство прошу приобщить к материалам дела.
                    </p>
                    <h4>Приложение:</h4>
                    <p>
                        1. Копия справки о временной регистрации по месту пребывания<br></br>
                        (либо страницы паспорта с пропиской)</p>
                </div>
            );
            
        };
        return (
            <div className='center'>
                <h2>Ходатайство о рассмотрении дела <br></br>
                    об административном правонарушении<br></br>
                    по месту жительства
                </h2>

                <p style={{fontSize:"17px", marginBottom:"30px"}}>Для создания ходатайства заполните поля формы ниже.</p>
                <form onSubmit={this.handleSubmit}>
                    <div className='formRow'>
                        <label>
                            <h3>Как вас зовут?</h3>
                            <p>Укажите ваше ФИО в родительном падеже</p>
                                <input
                                    name="name"
                                    type="text"
                                    placeholder="Иванова Ивана Ивановича"
                                    value={this.state.name}
                                    onChange={this.handleChange}
                                />
                                {this.state.nameError ? <div className='errorMessage'>{this.state.nameError}</div>: null}
                        </label>
                    </div>
                     


                    <div className='formRow'>
                        <label >
                            <h3>Ваш адрес?</h3>
                            <p>Укажите адрес прописки или регистрации</p>
                            <input
                                name="address"                                    type="text"
                                placeholder="г. Москва, ул. Тверская д. 1"
                                value={this.state.address}
                                onChange={this.handleChange}
                            />
                            {this.state.addressError ? <div className='errorMessage'>{this.state.addressError}</div>: null}
                        </label>
                    </div>
                        
                    <div className='formRow'>
                        <label >
                            <h3>Номер и адрес судебного участка</h3>
                            <p>Укажите сначала номер, а после адрес судебного участка</p>
                                <input
                                    name="courtName"
                                    type="text"
                                    placeholder="№1 Левобережного района г. Москвы"
                                    value={this.state.courtName}
                                    onChange={this.handleChange}
                                />
                                {this.state.courtNameError ? <div className='errorMessage'>{this.state.courtNameError}</div>: null}
                        </label>
                    </div>

                    <div className='formRow'>
                        <label >
                            <h3>Статьи КоАП РФ по которым идет рассмотрение</h3>
                            <p>Укажите сначала часть, а после номер статьи</p>
                                <input
                                    name="caseNumber"
                                    type="text"
                                    placeholder="ч.1 ст. 29.5 "
                                    value={this.state.caseNumber}
                                    onChange={this.handleChange}
                                />
                                {this.state.caseNumberError ? <div className='errorMessage'>{this.state.caseNumberError}</div>: null}
                        </label>
                    </div>

                    
                    <button className='button'>Создать</button>
                </form>
                <p style={{color:"grey", marginBottom:"0px"}}>
                    PS. Мы не сохраняем ваши данные
                </p>
            </div>
        );
    }
}
export default PetitionGenerator;